// section
.main-holder {
  overflow-x: hidden;
}
.section-wrapper {
  padding: 80px 0;
}
.sc-title {
  text-transform: uppercase;
  margin-bottom: 4.8rem;
  font-weight: bold;
  letter-spacing: 4px;
  position: relative;
  font-size: 24px;
  padding: 4px 0;
  position: relative;

  &::after {
    content: '';
    position: absolute;
    top: 100%;
    left: 0;
    height: 4px;
    width: 80px;
    background-color: $clr-orange;
  }
}

// opacity
.op {
  &-01 {
    opacity: 0.1;
  }
  &-02 {
    opacity: 0.2;
  }
  &-03 {
    opacity: 0.3;
  }
  &-04 {
    opacity: 0.4;
  }
  &-05 {
    opacity: 0.5;
  }
  &-06 {
    opacity: 0.6;
  }
  &-07 {
    opacity: 0.7;
  }
  &-08 {
    opacity: 0.8;
  }
  &-09 {
    opacity: 0.9;
  }
}

.breadcrumb {
  box-shadow: rgba(99, 99, 99, 0.1) 0px 2px 8px 0px;
  padding: 1rem 2rem;
  margin-bottom: 3.2rem;

  li {
    transition: $transition;
    &:hover {
      opacity: 0.9;
    }
  }
}
